'use strict'

// import axios from 'axios'


//测试
const _DO_MAN = 'http://dy.rmq168.com/';
const download_ewm_img = require('../assets/imgs/download-ewm-test.png');

//正式
// const _DO_MAN = 'https://szdy.rmq168.com/';
// const download_ewm_img = require('../assets/imgs/download-ewm.png');


const _HOST = _DO_MAN + 'api/';
const download_android_url = _DO_MAN + 'web/download/douyou.apk';
const download_ios_url = 'itms-services://?action=download-manifest&url=' + _DO_MAN + 'web/download/Info.plist';



axios.interceptors.request.use(config =>{
    return config
}, error =>{
    return Promise.reject(error)
})

axios.interceptors.response.use(response => {
    return response
}, error => {
    return Promise.resolve(error.response)
})

//成功處理函數
function handleSuccess(response){
    
    if (response &&response.data && response.data.code == 200 && (response.status === 200 || response.status === 304 || response.status === 400)){
        return {success: true, data:response.data.data,message:""};
    }else{
        return {success: false, data:[],message:response.data.msg};
    }
}

//失敗處理函數
function handleError(error){
   
    // let msg = '';
    // if(error.response && error.response.data && error.response.data.error_msg && error.response.data.error_msg != ''){
    //     msg = error.response.data.error_msg;
    // }else{
    //     msg = '请求服务器错误，请稍后再试'
    // }

    let msg = "请求服务器错误，请稍后再试";
    return {success: false, data:[],message:msg};
}


export default{
    _HOST,
    _DO_MAN,
    download_ewm_img,
    download_android_url,
    download_ios_url,
    get(url,token = sessionStorage.getItem("memebuy_httpToken") || ''){
        return axios({
            method: 'get',
            url: url,
            timeout: 10000,
            headers: {
                'X-Requested-With': 'XMLHttpRequest',
                'Authorization': 'Bearer ' + token,
            }
        }).then(res =>{
            return handleSuccess(res)
        }).catch(err =>{
            return handleError(err)
        })
    },
    post(url,data,token = sessionStorage.getItem("memebuy_httpToken") || ''){
        return axios({
            method: 'post',
            url: url,
            data: data,
            timeout: 10000,
            headers: {
                'X-Requested-With': 'XMLHttpRequest',
                'Content-Type': 'application/json; charset=UTF-8',
                // 'Authorization': 'Bearer ' + token,
            }
        }).then(res =>{
            return handleSuccess(res)
        }).catch(err =>{
            return handleError(err)
        })
    }
}